.app__aboutus {
  position: relative;
}

.app__aboutus-overlay {
  position: absolute;
  inset: 0;
}

.app__aboutus-overlay img {
  width: 620px;
  height: 500px;
  z-index: 0;
  opacity: 0.2;
}

.app__aboutus-content {
  width: 100%;
  z-index: 2;
}

.app__aboutus-content_about {
  flex: 1;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;
  text-align: right;
}

.app__aboutus-content_middleImage img {
  height: 910px;
}

.app__aboutus-content_history {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  text-align: left;
}

.app__aboutus-content_history img {
  transform: rotate(180deg);
}

.app__aboutus-content_history p,
.app__aboutus-content_about p {
  margin: 2rem 0;
  color: var(--color-grey);
}

@media screen and (max-width: 2000px) {
  .app__aboutus-content_middleImage {
    height: 900px;
    margin-bottom: 4rem;
  }
  .app__aboutus-content_history p,
  .app__aboutus-content_about p {
  margin: 4rem 0;
  }
  .app__aboutus-content_middleImage img{
    height: 650px;
  }
}

@media screen and (max-width: 900px) {
  .app__aboutus-content {
    flex-direction: column;
  }
  .app__aboutus-content_middleImage {
    margin: 2rem 0;
  }
  .app__aboutus-content_middleImage img {
    height: 500px;
  }
}

@media screen and (max-width: 650px) {
  .app__aboutus-overlay img {
    height: 280px;
    width: 360px;
  }
  .app__aboutus-content_middleImage img {
    height: 410px;
  }
  .app__aboutus-content_middleImage {
    margin: 0;
  }
}

@media screen and (max-width: 350px) {
  .app__aboutus-overlay img {
    height: 210px;
    width: 240px;
  }
  .app__aboutus-content_middleImage img {
    height: 310px;
  }
  .app__aboutus-content_middleImage {
    margin: 0;
  }
}